.shapedBtns-contianer {
    display: absolute;
    top: 20%;
    left: 5%;
    min-height: 20rem;
    min-width: 20rem;
  }
  
  .btn-m-container {
    display: flex;
    background-color: $color-red;
    width: 140px;
    height: 140px;
    position:absolute;
    top: 45%;
    left: 46%;
    transform: rotate(45deg);
  
  
    & .linkm-text {
        @include text-fix;
    }
  }
  
  .btn-t-container {
    display: flex;
    background-color: $color-red;
    width: 14rem;
    height: 14rem;
    position: absolute;
    top: 45%;
    left: 64%;
    transform: rotate(45deg);

    & .linkt-text {
        @include text-fix;
      }
  }
  
  .register-container {
    display: flex;
    background-color: black;
    width: 14rem;
    height: 14rem;
    position: absolute;
    top: 30%;
    left: 55%;
    transform: rotate(45deg);

    &  .register-text {
        @include text-fix;
      }
  }
 
  .image-container {
    display: flex;
    width: 14rem;
    height: 14rem;
    position: absolute;
    top: 60%;
    left: 55%;
    transform: rotate(45deg);

    & .image-man {
        position: absolute;
        top: 5%;
        left: 20%;
        transform: rotate(-45deg);
      }
  }

  