.LoginForm {
    width: 40rem;
    padding: 4rem;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -10%);
    background: #e28f8f67;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0 3rem 3rem;

  
  & input[type="text"],
  & input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 2rem auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 1.4rem 1rem;
    width: 20rem;
    outline: none;
    color: rgb(63, 15, 15);
    font-size: $default-font-size;
    border-radius: 2.4rem;
    transition: 0.25s;
  }
  
  & input[type="text"]:focus,
  & input[type="password"]:focus {
    width: 28rem;
    border-color: $register-green;
  }
  
  & input[type="submit"] {
    border: 0;
    background: none;
    display: block;
    margin: 2rem auto;
    text-align: center;
    border: 2px solid $register-green;
    padding: 1.4rem 25px;
    outline: none;
    color: rgb(63, 15, 15);
    border-radius: 2.4rem;
    transition: 0.25s;
    font-size: 1.7rem;
    font-family: "Libre Baskerville", serif;

    &:hover {
        background: $register-green;
    }
  }
  
}