.units-heading {
  margin-top: 5rem !important;
}
.select-container--options {
    width: 110%;
    height: 5rem;
    border-radius: 1rem;
    font-size: 2.6rem;
    background-color: $button-color;
    color: $color-primary;
    padding: 0px 10px;

    &:active {
      color: red;
      border-radius: 1rem 1rem 0 0;
    }
  }
  
  .choose-btn {
    left: 0%;
  }

  .search-units {
    margin-top: 5rem !important;
    width: 60%;
  }
  